<template>
  <div>
    <div class="xz_con">
      <i v-if="c_value.trim() != ''" class="delete_value" @click="clearReturn"></i>
      <input type="text" readonly class="trigger_select" @click="region_select = !region_select" v-model="c_value"
        placeholder="">
    </div>
    <div class="xz_selector" v-show="region_select" ref="selector" @click="showResult = false">
      <div class="xz_domain">
        <i class="xz_close" @click="xz_close"></i>
        <div v-if="search" class="xz_search_input">
          <input type="text" placeholder="" v-model="searchField" @input="searchMethod" @click.stop="">
          <div class="search_result_con" v-show="showResult" @click.stop="">
            <div class="search_result">
              <div class="search_line" v-if="searchProvince.length > 0">
                <h1>/</h1>
                <div class="search_item_con">
                  <div class="search_item" v-for="(province) in searchProvince" :key="province.code">
                    <span ref="sProvince" @click="sProvince(province.code)">{{province.name}}</span>
                  </div>
                </div>
              </div>
              <div class="search_line" v-if="searchCity.length > 0 && showCityTab">
                <h1></h1>
                <div class="search_item_con">
                  <div class="search_item" v-for="(city) in searchCity" :key="city.code">
                    <span ref="sCity" @click="sCity(city.code)">{{city.name}}</span>
                  </div>
                </div>
              </div>
              <div class="search_line" v-if="searchArea.length > 0 && showAreaTab">
                <h1>/</h1>
                <div class="search_item_con">
                  <div class="search_item" v-for="(area) in searchArea" :key="area.code">
                    <span ref="sArea" @click="sArea(area.code)">{{area.name}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="xz_tabs">
          <!---->
          <button class="multpleReturn" @click="multpleReturn"
            v-if="!provinceGet && cityGet && multipleCity"></button>
          <!---->
          <button class="multpleReturn" @click="returnSingle"
            v-if="fullrange && !multipleCity"></button> <!--multipleCity true-->
          <button class="clearChoose" @click="clear" v-if="!provinceGet && cityGet && multipleCity"></button>
          <ul ref="region">
            <li :class="c_region==0?'xz_choose':''">/</li>
            <li :class="c_region==1?'xz_choose':''" v-if="showCityTab"></li>
            <li :class="c_region==2?'xz_choose':''" v-if="showAreaTab">/</li>
            <li v-if="fullrange && !multipleCity">
              : 
              <span v-if="isEmptyObj(provinceChoose)">{{provinceChoose.name}}</span>
              <span v-if="isEmptyObj(cityChoose)"> / {{cityChoose.name}}</span>
              <span v-if="isEmptyObj(areaChoose)"> / {{areaChoose.name}}</span>
            </li>
          </ul>
        </div>
        <div class="xz_total_domain">
          <!---->
          <div class="xz_region" v-show="c_region==0" ref="province_choose">
            <span class="single_city" v-for="(p,index) in province" :key="p.code">
              <i @click="chooseProvince(index,p)" :data-code="p.code" ref="province_btn">{{p.name}}</i>
            </span>
          </div>
          <!---->
          <div class="xz_region" v-show="c_region==1" ref="city_choose">
            <span class="single_city" v-for="(city,index) in citys" :key="city.code">
              <i @click="chooseCity(index,city)" :data-code="city.code" ref="city_btn">{{city.name}}</i>
              <!---->
              <!-- <span class="xz_checkbox" v-if="!provinceGet && cityGet && multipleCity">
                <input type="checkbox" ref="chooseBox">
                <span class="xz_custom_checkbox"></span>
              </span> -->
            </span>
          </div>
          <!---->
          <div class="xz_region" v-show="c_region==2">
            <span class="single_city" v-for="(area,index) in areaList" :key="area.code">
              <i @click="chooseArea(index,area)" :data-code="area.code" ref="area_btn">{{area.name}}</i>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Data from './data'; //
  export default {
    props: {
      hotCity: {
        type: Array,
        default: function () {
          return new Array()
        }
      },
      multipleCity: {
        // false
        //
        type: Boolean,
        default: false
      },
      //
      selected: '',
      //
      search: {
        type: Boolean,
        default: false
      },
      //
      provinceGet: {
        type: Boolean,
        default: false
      },
      cityGet: {
        type: Boolean,
        default: false,
      },
      areaGet: {
        type: Boolean,
        default: true
      },
      //
      fullrange:{  //true
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        // 
        region_select: false,
        //
        c_value: '',
        province: {}, //
        city: [], //
        area: [], //
        domain: [], // ()
        // code 
        testHot: ['130300', '220300', '441400', '460100', '511900', '530800', '610100'],
        //
        citys: [],
        //
        areaList: [],
        //
        c_region: 0,
        //index, 
        provinceIndex: 0,
        //
        provinceChoose: {}, //
        cityChoose: {}, //
        cityChooseArr: [], //
        areaChoose: {}, //
        //
        searchField: '',
        //
        searchProvince: [],
        searchCity: [],
        searchArea: [],
        //
        showResult: false,
        //
        rangeSet:0
      }
    },
    created() {
      this.province = Data.province;
      this.city = Data.city;
      this.area = Data.area;
      // ()

      //
      for (let i = 0; i < this.province.length; i++) {
        // 
        const range = {
          province: this.province[i]
        };
        //
        const provinceCode = this.province[i].code.substr(0, 2);
        const city = []; //
        const area = []; //
        //
        for (let ii = 0; ii < this.city.length; ii++) {
          if (this.city[ii].code.substr(0, 2) == provinceCode) {
            //
            city.push(this.city[ii]);
          }
        }
        //110000
        //code 100
        if (city.length == 0) {
          const code = this.province[i].code;
          range.city = [{
            name: this.province[i].name,
            code: this.province[i].code.replace(/.{3}$/, '100'), //
          }]
        } else {
          range.city = city;
        }
        //
        //00
        for (let a = 0; a < this.area.length; a++) {
          if (this.area[a].code.substr(0, 2) == provinceCode && (this.area[a].code.substr(this.area[a].code.length - 2,
              2) != '00')) {
            area.push(this.area[a]);
          }
        }
        range.area = area;
        this.domain.push(range);
      }
    },
    mounted() {
      const tabs = this.$refs.region.getElementsByTagName("li");
      const _this = this;
      // //provinceGet cityGet areaGet

      if (this.areaGet) { // 

      } else if (this.cityGet) { // 
        //

      } else if (this.provinceGet) { // 
        //
      }
      //tab
      for (let i = 0; i < tabs.length; i++) {
        tabs[i].addEventListener('click', function () {
          if(i == 3) return;
          //
          _this.c_region = i;
        }.bind(tabs[i]), false);
      }
      //
      this.initRegion(this.selected);

    },
    watch: {
      'selected': {
        handler: function () {
          this.initRegion(this.selected);
        }
      }
    },
    computed: {
      showAreaTab() { //tab
        if (this.provinceGet || this.cityGet) { //
          return false;
        } else {
          return true
        }
      },
      showCityTab() { //tab
        if (this.provinceGet) { //
          return false;
        } else {
          return true;
        }
      }
    },
    methods: {
      //
      chooseProvince(index, choice) {
        if (this.provinceChoose.code == choice.code) { //
          return;
        }

        //
        this.cityChoose = {}; //
        this.cityChooseArr = []; //
        this.areaChoose = {}; //
        //
        this.provinceChoose = choice;

        const _this = this;
        //  class, classclass
        if (this.$refs.province_btn[index].classList.value.indexOf('choosedProvince') == -1) {
          this.areaList = [];
          //class
          this.removeClass(this.$refs.province_btn, 'choosedProvince');
          //class
          this.$refs.province_btn[index].classList.add('choosedProvince');
          //province
          //
          for (let i = 0; i < this.domain.length; i++) {
            if (this.domain[i].province.code == choice.code) {
              //
              this.provinceIndex = i;
              //
              this.citys = this.domain[i].city;
              break;
            }
          }
          //////
          //
          setTimeout(() => {
            //
            if (_this.provinceGet) { //
              //
              _this.region_select = false;
              ////
              _this.returnRegion();
            } else { //
              _this.c_region = 1;
            }
          }, 150)
        }
      },
      //
      chooseCity(index, choice) {
        //
        if (this.multipleCity && this.cityGet) { //
        } else {
          if(choice.code != this.cityChoose.code){
            this.areaChoose = {};
          }
          this.cityChoose = choice;
        }
        const _this = this;
        //  class, classclass
        if (this.$refs.city_btn[index].classList.value.indexOf('choosedProvince') == -1 || this.multipleCity) {
          if (!this.cityGet || this.fullrange) { //
            //class
            this.removeClass(this.$refs.city_btn, 'choosedProvince');
            //class
            this.$refs.city_btn[index].classList.add('choosedProvince');
          } else if (this.cityGet && this.multipleCity) { //
            if (this.$refs.city_btn[index].classList.contains('choosedProvince')) {
              this.$refs.city_btn[index].classList.remove('choosedProvince');
              //
              this.cityChooseArr.forEach((city, index) => {
                if (city.code == choice.code) {
                  this.cityChooseArr.splice(index, 1);
                }
              })
            } else {
              this.$refs.city_btn[index].classList.add('choosedProvince');
              //
              this.cityChooseArr.push(choice);
            }
          }

          //
          this.areaList = [];
          if (!_this.cityGet && !_this.provinceGet) { //
            //city
            ///
            //4
            const area = this.domain[this.provinceIndex].area; //area
            const consist = choice.code.substr(0, 4);
          
            area.forEach((item) => {
              if (item.code.substr(0, 4) == consist) this.areaList.push(item);
            })
            
          }
          //////
          //
          setTimeout(() => {
            //
            if (_this.cityGet) { //
              ////
              if (!_this.multipleCity) { //
                if(!this.fullrange){
                  _this.region_select = false;
                  _this.returnRegion();
                  //
                }
              }
            } else { //
              _this.c_region = 2;
            }
          }, 150)
        }
      },
      //
      chooseArea(index, choice) {
        //
        this.areaChoose = choice;
        if (this.$refs.area_btn[index].classList.value.indexOf('choosedProvince') == -1) {
          //class
          this.removeClass(this.$refs.area_btn, 'choosedProvince');
          //class
          this.$refs.area_btn[index].classList.add('choosedProvince');
        }
        if(!this.fullrange){  //fullrangefalse
           //
          this.region_select = false;
           ////
          this.returnRegion();
        }
      },

      //
      initRegion(region) {
        //province,
        if (this.selected) { //
          //
          let codeData = '';
          let isProvince = false;
          let isCity = false;
          let isArea = false;
          let provincename;
          let cityname;
          let citynameArr = []; //
          let areaname;
          //
          Array.isArray(this.selected) ? codeData = this.selected[0] : codeData = this.selected;
          //code 
          if (codeData.slice(-4) == '0000') { //
            isProvince = true;
          } else if (codeData.slice(-4) != '0000' && codeData.slice(-2) == '00') { //
            isCity = true;
          } else { //
            isArea = true;
          }
          //,code
          if (isProvince && (this.provinceGet || this.fullrange)) { //code 
            for (let i = 0; i < this.domain.length; i++) {
              if (this.domain[i].province.code == codeData) {
                //
                provincename = this.domain[i].province;
                this.provinceChoose = provincename;
                if(this.fullrange && !this.provinceGet){
                  //
                  this.citys = this.domain[i].city;
                }
                //
                this.provinceIndex = i;
                break;
              }
            }
            //
            this.$nextTick(() => {
              for (let i = 0; i < this.$refs.province_btn.length; i++) {
                if (this.$refs.province_btn[i].dataset.code == codeData) {
                  //class
                  this.removeClass(this.$refs.province_btn, "choosedProvince");
                  this.$refs.province_btn[i].classList.add("choosedProvince");
                  this.c_value = provincename.name;
                  break;
                }
              }
            })
          }
          if (isCity && ((this.cityGet && !this.provinceGet) || this.fullrange)) { //code  
            for (let i = 0; i < this.domain.length; i++) {
              let code2 = codeData.substr(0, 2); //
              if (this.domain[i].province.code.substr(0, 2) == code2) {
                //
                provincename = this.domain[i].province;
                //
                this.citys = this.domain[i].city;
                this.provinceChoose = provincename;
                //
                this.provinceIndex = i;
                
                break;
              }
            }
            //
            let isMultple = Array.isArray(this.selected);
            if (isMultple && this.multipleCity) { //
              let _this = this;
              let hasCity = false;
              let indexArr = [];
              this.selected.forEach((city) => {
                for (let c = 0; c < _this.citys.length; c++) {
                  if (city == _this.citys[c].code) {
                    if (!hasCity) hasCity = true; //
                    indexArr.push(c);
                  }
                }
              })
              if (hasCity) {
                this.c_region = 1;
                this.$nextTick(() => {
                  //class
                  let nameList = '';
                  this.removeClass(this.$refs.province_btn, "choosedProvince");
                  this.removeClass(this.$refs.city_btn, "choosedProvince");
                  //
                  this.$refs.province_btn[this.provinceIndex].classList.add("choosedProvince");
                  this.cityChooseArr = []; //
                  indexArr.forEach((arr) => {
                    _this.$refs.city_btn[arr].classList.add("choosedProvince");
                    _this.cityChooseArr.push(_this.citys[arr]);
                    if (arr != indexArr.length - 1) {
                      nameList += `${_this.citys[arr].name}, `;
                    } else {
                      nameList += `${_this.citys[arr].name}`;
                    }

                    this.c_value = `${provincename.name} (${nameList})`;

                  })
                })
              }
            } else {
              for (let c = 0; c < this.citys.length; c++) {
                if (this.citys[c].code == codeData) {
                  this.cityChoose = this.citys[c];
                  this.$nextTick(() => {
                    //class
                    this.removeClass(this.$refs.province_btn, "choosedProvince");
                    this.removeClass(this.$refs.city_btn, "choosedProvince");
                    //
                    this.$refs.province_btn[this.provinceIndex].classList.add("choosedProvince");
                    //
                    this.$refs.city_btn[c].classList.add("choosedProvince");
                    this.c_region = 1;

                    this.c_value = `${provincename.name}-${this.citys[c].name}`;
                  })
                  break;
                }
              }
            }
          }
          if (isArea && !this.cityGet && !this.provinceGet) { //code,
            let code2 = codeData.substr(0, 2); //
            let code4 = codeData.substr(0, 4); //
            let drawA = 0; //index
            let drawC = 0; //index
            for (let i = 0; i < this.domain.length; i++) {
              if (this.domain[i].province.code.substr(0, 2) == code2) {
                //
                provincename = this.domain[i].province;
                this.provinceChoose = provincename;
                //
                this.provinceIndex = i;
                //
                this.citys = this.domain[i].city;
                //
                let areaArr = [];
                for (let a = 0; a < this.domain[i].area.length; a++) { //
                  if (this.domain[i].area[a].code.substr(0, 4) == code4) {
                    areaArr.push(this.domain[i].area[a]);
                  }
                }
                for (let c = 0; c < areaArr.length; c++) { //cindex
                  if (areaArr[c].code == codeData) { //
                    //
                    this.areaList = areaArr;
                    this.areaChoose = areaArr[c];
                    for (let c2 = 0; c2 < this.citys.length; c2++) {
                      if (this.citys[c2].code.substr(0, 4) == code4) {
                        this.cityChoose = this.citys[c2];
                        drawC = c2;
                        break;
                      }
                    }
                    drawA = c;
                    this.c_region = 2;
                    this.$nextTick(() => {
                      //class
                      this.removeClass(this.$refs.province_btn, "choosedProvince");
                      this.removeClass(this.$refs.city_btn, "choosedProvince");
                      this.removeClass(this.$refs.area_btn, "choosedProvince");
                      //
                      this.$refs.province_btn[i].classList.add("choosedProvince");
                      //
                      this.$refs.city_btn[drawC].classList.add("choosedProvince");
                      //
                      this.$refs.area_btn[c].classList.add("choosedProvince");
                      this.c_value = `${provincename.name}-${this.citys[drawC].name}-${areaArr[c].name}`;
                    })
                    break;
                  }
                }
              }
            }
          }
        } else {
          this.clear();
        }
      },
      //
      xz_close() {
        this.region_select = false;
        //
        //this.clear();
      },
      //,
      clear() {
        //
        this.provinceChoose = {};
        this.cityChoose = {};
        this.cityChooseArr = [];
        this.areaChoose = {};
        this.c_value = ''; //
        this.c_region = 0; //
        this.rangeSet = 0; //0
        this.searchField = '';
        //
        this.searchProvince = [];
        this.searchCity = [];
        this.searchArea = [];
        //class
        this.removeClass(this.$refs.province_btn, "choosedProvince");
        //
        this.citys = [];
        //
        this.areaList = [];
      },
      clearReturn() {
        this.clear();
        let region = {
          province: {
            code: '',
            name: ''
          },
          city: {
            code: '',
            name: ''
          },
          area: {
            code: '',
            name: ''
          }
        }
        this.$emit("cRegion", region);
      },
      //
      returnRegion() {
        let region;
        if (((!this.provinceGet) && (!this.cityGet) || (this.fullrange && this.rangeSet == 3)) && this.isEmptyObj(this.provinceChoose) && this.isEmptyObj(this.cityChoose) && this.isEmptyObj(this.areaChoose)) { //   //
          region = {
            province: this.provinceChoose,
            city: this.cityChoose,
            area: this.areaChoose
          }
          //
          this.c_value = `${this.provinceChoose.name}-${this.cityChoose.name}-${this.areaChoose.name}`;
        } else if ((!this.provinceGet) && this.cityGet || (this.fullrange && this.rangeSet == 2)) { //  
          region = {
            province: this.provinceChoose,
            city: this.cityChoose
          }
          //
          this.c_value = `${this.provinceChoose.name}-${this.cityChoose.name}`;
        } else if (this.provinceGet || (this.fullrange && this.rangeSet == 1)) { //  
          region = {
            province: this.provinceChoose
          }
          //
          this.c_value = this.provinceChoose.name;
        }

        //
        this.$emit("cRegion", region);
      },
      //
      multpleReturn() {
        if (this.cityChooseArr.length == 0) return; //
        let region = {
          province: this.provinceChoose,
          city: this.cityChooseArr
        }
        //
        let cityName = '';
        this.cityChooseArr.forEach((city, index) => {
          if (index != this.cityChooseArr.length - 1) {
            cityName += `${city.name}, `;
          } else {
            cityName += city.name;
          }
        })

        this.c_value = `${this.provinceChoose.name} (${cityName})`;
        //
        this.region_select = false;
        this.$emit("cRegion", region);
      },
      //
      returnSingle(){ //fullrange
        //,return
        if(!this.isEmptyObj(this.provinceChoose)) return;
        //
        if(this.isEmptyObj(this.provinceChoose) && this.isEmptyObj(this.cityChoose) && this.isEmptyObj(this.areaChoose)){
          this.rangeSet = 3;
          //
          this.returnRegion();
          //
          this.region_select = false;
          return;
        }
        if(this.isEmptyObj(this.provinceChoose) && this.isEmptyObj(this.cityChoose)){
          this.rangeSet = 2;
          //
          this.returnRegion();
          //
          this.region_select = false;
          return;
        }
        if(this.isEmptyObj(this.provinceChoose)){
          this.rangeSet = 1;
          //
          this.returnRegion();
          //
          this.region_select = false;
          return;
        }
        
      },
      //
      isEmptyObj(obj){
        let result = null;
        Object.keys(obj).length > 0 ? result = true : result = false;
        return result
      },
      //class
      removeClass(lists, classname) {
        for (let i = 0; i < lists.length; i++) { //class
          if (lists[i].classList.value.indexOf(classname) != -1) lists[i].classList.remove(classname);
        }
      },
      //
      searchMethod() {
        if (this.searchField.trim() != '') {
          //
          this.searchProvince = [];
          this.searchCity = [];
          this.searchArea = [];
          if ((!this.provinceGet) && (!this.cityGet)) { //
            //
            for (let i = 0; i < this.area.length; i++) {
              if (this.area[i].name.indexOf(this.searchField) != -1) {
                this.searchArea.push(this.area[i]);
              }
            }
          }

          if (!this.provinceGet) { //
            //
            for (let i = 0; i < this.city.length; i++) {
              if (this.city[i].name.indexOf(this.searchField) != -1) {
                this.searchCity.push(this.city[i]);
              }
            }
          }

          //
          for (let i = 0; i < this.province.length; i++) {
            if (this.province[i].name.indexOf(this.searchField) != -1) {
              this.searchProvince.push(this.province[i]);
            }
          }

          if (this.searchProvince.length > 0 || this.searchCity.length > 0 || this.searchArea.length > 0) this
            .showResult = true;

        } else {
          this.showResult = false;
          this.searchProvince = [];
          this.searchCity = [];
          this.searchArea = [];
        }
      },
      //
      sProvince(code) {
        this.c_region = 0;
        //
        this.areaList = [];
        this.citys = [];
        this.showResult = false;
        this.searchField = '';
        //
        const code2 = code.substr(0, 2); //
        for (let i = 0; i < this.province.length; i++) {
          if (this.province[i].code.substr(0, 2) == code2) {
            this.provinceChoose = this.province[i]; //
            this.provinceIndex = i; //
            this.removeClass(this.$refs.province_btn, "choosedProvince");
            this.$refs.province_btn[i].classList.add("choosedProvince");
            //
            this.citys = this.domain[i].city;
            if (this.provinceGet) { //
              //
              this.region_select = false;
              this.returnRegion();
            }
            break;
          }
        }
      },
      sCity(code) {
        this.c_region = 1;
        //
        this.areaList = [];
        this.citys = [];
        this.showResult = false;
        this.searchField = '';
        const code2 = code.substr(0, 2); //
        const code4 = code.substr(0, 4); //
        let provinceIndex;
        for (let i = 0; i < this.province.length; i++) {
          if (this.province[i].code.substr(0, 2) == code2) {
            this.provinceChoose = this.province[i]; //
            this.removeClass(this.$refs.province_btn, "choosedProvince");
            this.$refs.province_btn[i].classList.add("choosedProvince");
            provinceIndex = i; //
            this.provinceIndex = i;
            //
            this.citys = this.domain[i].city;
            this.$nextTick(() => {
              for (let i = 0; i < this.citys.length; i++) {
                if (this.citys[i].code.substr(0, 4) == code4) {
                  this.cityChoose = this.citys[i]; //
                  this.removeClass(this.$refs.city_btn, "choosedProvince");
                  this.$refs.city_btn[i].classList.add("choosedProvince");

                  if (this.cityGet) { //
                    if (!this.multipleCity && !this.fullrange) {
                      //
                      this.region_select = false;
                      this.returnRegion();
                    }
                  }
                  /// ()
                  if ((!this.provinceGet) && (!this.cityGet)) {
                    this.domain[provinceIndex].area.forEach((item) => {
                      if (item.code.substr(0, 4) == code4) this.areaList.push(item);
                    })
                  }
                  break;
                }
              }
            })
            break;
          }
        }
      },
      sArea(code) { //
        this.showResult = false;
        this.c_region = 2;
        this.searchField = '';
        
        //
        this.areaList = [];
        this.citys = [];

        //
        const code2 = code.substr(0, 2); //
        const code4 = code.substr(0, 4); //
        let provinceIndex;
        for (let i = 0; i < this.province.length; i++) {
          if (this.province[i].code.substr(0, 2) == code2) {
            this.provinceChoose = this.province[i]; //
           //index
            this.provinceIndex = i;
            this.removeClass(this.$refs.province_btn, "choosedProvince");
            this.$refs.province_btn[i].classList.add("choosedProvince");
            provinceIndex = i; //
            this.provinceIndex = i;
            //
            this.citys = this.domain[i].city;
            this.$nextTick(() => {
              for (let i = 0; i < this.citys.length; i++) {
                if (this.citys[i].code.substr(0, 4) == code4) {
                  this.cityChoose = this.citys[i]; //
                  this.removeClass(this.$refs.city_btn, "choosedProvince");
                  this.$refs.city_btn[i].classList.add("choosedProvince");
                  ///
                  this.domain[provinceIndex].area.forEach((item) => {
                    if (item.code.substr(0, 4) == code4) this.areaList.push(item);
                  })
                  for (let i = 0; i < this.areaList.length; i++) {
                    if (this.areaList[i].code == code) {
                      this.areaChoose = this.areaList[i]; //
                      this.$nextTick(() => {
                        this.removeClass(this.$refs.area_btn, "choosedProvince");
                        this.$refs.area_btn[i].classList.add("choosedProvince");
                      })
                      //
                      if(!this.fullrange){
                         this.returnRegion();
                         //
                        this.region_select = false;
                      }
                      
                      break;
                    }
                  }
                  break;
                }
              }
            })
            break;
          }
        }
      }
    }
  }
</script>
<style scoped>
  .xz_con {
    width: 270px;
    position: relative;
  }

  .search_line {
    width: 100%;
  }

  .search_line h1 {
    font-size: 14px;
    padding: 5px 0 5px 10px;
    margin: 0 0 5px 0;
    width: 100%;
    box-sizing: border-box;
    border-bottom: 1px solid #ddd;
  }

  .search_item_con {
    width: 90%;
    margin: 0 auto 10px auto;
    display: flex;
    flex-wrap: wrap;
  }

  .search_item {
    width: 33%;
  }

  .search_item span {
    padding: 5px 5px;
    margin-bottom: 5px;
    font-size: 13px;
    display: inline-block;
    white-space: nowrap;
    cursor: pointer;
  }

  .search_item span:hover {
    background: #5f4b8b;
    color: #fff !important;
  }

  .xz_selector i {
    color: #424a5e;
    font-size: 14px;
  }

  .delete_value {
    position: absolute;
    right: 15px;
    top: 15px;
    display: block;
    width: 13px;
    height: 13px;
    cursor: pointer;
  }

  .delete_value::before {
    position: absolute;
    content: '';
    display: block;
    width: 13px;
    height: 2px;
    transform-origin: 50% 50%;
    background: #424a5e;
    left: 0px;
    top: 7px;
    border-radius: 20px;
    transform: rotatez(130deg);
  }

  .delete_value::after {
    position: absolute;
    content: '';
    display: block;
    width: 13px;
    height: 2px;
    transform-origin: 50% 50%;
    background: #424a5e;
    left: 0px;
    top: 7px;
    border-radius: 20px;
    transform: rotatez(50deg);
  }

  .trigger_select {
    width: 100%;
    height: 45px;
    box-sizing: border-box;
    padding: 0 28px 0 15px;
    border-radius: 5px;
    border: 1px solid #ddd;
    outline: none;
    font-size: 14px;
    background: #fff;
    cursor: pointer;
    color: #424a5e;
  }

  .xz_tabs {
    width: 100%;
    padding-left: 15px;
    box-sizing: border-box;
    background: #f5f5f5;
    border-bottom: 1px solid #e6e7e7;
    position: relative;
  }

  .multpleReturn {
    position: absolute;
    font-size: 14px;
    color: #fff;
    border-radius: 5px;
    width: 100px;
    height: 80%;
    top: 10%;
    background: #5f4b8b;
    right: 2%;
    outline: none;
    border: none;
    cursor: pointer;
    z-index: 10;
  }

  .clearChoose {
    position: absolute;
    font-size: 14px;
    color: #fff;
    border-radius: 5px;
    width: 100px;
    height: 80%;
    top: 10%;
    background: #c82a2e;
    right: 135px;
    outline: none;
    border: none;
    cursor: pointer;
    z-index: 10;
  }

  .xz_tabs ul {
    overflow: hidden;
    padding-left: 0;
    margin: 0;
    position: relative;
    top: 1px;
  }

  .xz_tabs ul li {
    float: left;
    padding: 8px 20px;
    list-style: none;
    font-size: 14px;
    line-height: 25px;
    color: #424a5e;
    cursor: pointer;
    border: 1px solid transparent;
  }

  .xz_province {
    background: #f9fafb;
    height: 100%;
    overflow-y: scroll;
    width: 155px;
    box-sizing: border-box;
  }

  .xz_province ul {
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .xz_choose {
    background: #fff !important;
    border-top-color: #e6e7e7 !important;
    border-left-color: #e6e7e7 !important;
    border-right-color: #e6e7e7 !important;
  }

  .xz_province li {
    width: 100%;
    box-sizing: border-box;
    list-style: none;
    cursor: pointer;
    text-align: left;
    font-size: 14px;
    line-height: 25px;
    padding: 10px 0 10px 30px;
    color: #424a5e;
    position: relative;
  }

  .xz_province .choosedProvince::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 5px;
    background: #5f4b8b;
  }

  .choosedProvince {
    color: #fff !important;
    background: #5f4b8b;
  }


  .xz_province li:hover {
    background: #fff;
    color: #5f4b8b;
  }

  .xz_selector {
    position: fixed;
    z-index: 9999;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(37, 40, 48, 0.7);
    display: flex;
    align-items: center;
  }

  .xz_domain {
    width: 850px;
    background: #f5f5f5;
    margin: 0 auto;
    height: 450px;
    padding: 20px 0 0 0;
    position: relative;
  }

  .xz_close {
    position: absolute;
    top: 15px;
    right: 15px;
    display: block;
    width: 20px;
    height: 20px;
    cursor: pointer;
    z-index: 100;
  }

  .xz_close::after {
    content: '';
    width: 20px;
    height: 2px;
    display: block;
    background: #424a5e;
    position: absolute;
    left: 0;
    top: 3px;
    transform-origin: 100%;
    transform: rotatez(-45deg);
  }

  .xz_close::before {
    content: '';
    width: 20px;
    height: 2px;
    display: block;
    background: #424a5e;
    position: absolute;
    right: -6px;
    top: 3px;
    transform-origin: 0;
    transform: rotatez(45deg);
  }

  .xz_search_input {
    padding: 0 20px;
    margin-bottom: 20px;
    position: relative;
  }

  .xz_search_input input {
    width: 180px;
    font-size: 13px;
    border-radius: 30px;
    height: 30px;
    border: 1px solid #424a5e;
    outline: none;
    box-sizing: border-box;
    padding: 0 15px;
    color: #424a5e;
  }

  .xz_search_input .search_result_con {
    position: absolute;
    left: 15px;
    top: 45px;
    z-index: 200;
  }

  .xz_search_input .search_result {
    min-width: 200px;
    max-width: 700px;
    min-height: 100px;
    max-height: 250px;
    overflow-y: auto;
    border: 1px solid #ddd;
    background: #fff;
  }

  .xz_search_input .search_result_con::before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    left: 80px;
    top: -19px;
    border: 10px solid #fff;
    border-top: 10px solid transparent;
    border-left-color: transparent;
    border-right-color: transparent;
  }

  .xz_total_domain {
    width: 100%;
    height: 400px;
    display: flex;
  }

  .xz_region {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    padding: 0 35px;
    box-sizing: border-box;
    background: #ffffff;
  }

  .xz_region .single_city {
    display: flex;
    float: left;
    align-items: center;
    width: 25%;
    cursor: pointer;
    font-size: 14px;
    color: #424a5e;
    line-height: 25px;
    padding: 5px 0;
  }

  .xz_region .single_city i {
    font-style: normal;
    padding: 3px 8px;
  }

  .xz_region .single_city:hover {
    color: #5f4b8b;
  }

  .xz_checkbox {
    position: relative;
    width: 15px;
    height: 15px;
    display: inline-block;
    margin-left: 5px;
  }

  .xz_custom_checkbox {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    background: #fff;
    border: 1px solid #ddd;
    box-sizing: border-box;
  }

  .xz_checkbox input[type="checkbox"] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    margin: 0;
    opacity: 0;
    z-index: 2;
    cursor: pointer;
  }

  .xz_checkbox input[type="checkbox"]:checked+.xz_custom_checkbox::before {
    content: '';
    position: absolute;
    left: 4px;
    top: 5px;
    background: #424a5e;
    width: 2px;
    height: 7px;
    border-radius: 10px;
    transform: rotateZ(-40deg);
  }

  .xz_checkbox input[type="checkbox"]:checked+.xz_custom_checkbox::after {
    content: '';
    position: absolute;
    right: 3px;
    top: 1px;
    background: #424a5e;
    width: 2px;
    height: 10px;
    transform: rotateZ(26deg);
    border-radius: 10px;
  }
</style>


